#site-navigation {

  ul {
    @include list-reset;
  }

  #primary-menu {
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-start;

    > li {
      margin-right: cre(20px);

      &:last-child {
        margin-right: 0;
      }
    }
  }
}

// Burger Button
