// Rem output with px fallback
@mixin font-size($sizeValue: 1) {
	font-size: ($sizeValue * 16) * 1px;
	font-size: $sizeValue * 1rem;
}

// Center block
@mixin center-block {
	display: block;
	margin-left: auto;
	margin-right: auto;
}

// Clearfix
@mixin clearfix() {
	content: "";
	display: table;
	table-layout: fixed;
}

// Clear after (not all clearfix need this also)
@mixin clearfix-after() {
	clear: both;
}

// Column width with margin
@mixin column-width($numberColumns: 3) {
	width: map-get( $columns, $numberColumns ) - ( ( $columns__margin * ( $numberColumns - 1 ) ) / $numberColumns );
}

// ================================
// Calculate rem sizes
// ================================

@function cre($target, $context: 16) {
  @return calc-rem-size($target, $context)
}

@function calc-rem-size($target, $context: 16) {
  $strip-target: su($target);
  $strip-context: su($context);
  @return ($strip-target / $strip-context) * 1rem;
}

// ================================
// Strip Units
// ================================

// Strip the units from a value:
// css-tricks.com/snippets/css/less-mixin-for-rem-font-sizing

@function su($num) {
  @return strip-unit($num);
}

@function strip-unit($num) {
  @return $num / ($num * 0 + 1);
}

// ================================
// Compartment max width
// ================================
@mixin compartment($value: ($boundary * 1px)) {
  position: relative;
  margin: 0 auto;
  max-width: $value;
}

.container {
  @include compartment;
}

// ================================
// List Reset
// ================================

// Example usage:
// nav {
//   @include list-reset;
// }
// nav ul {
//   @include list-reset;
// }
// ul { @include list-rest}

@mixin list-reset {

  &, ul {
    margin: 0;
    padding: 0;
    list-style: none;
  }

    li {
       margin: 0;
       padding: 0;
    }

}